<template>
  <div class="flex xl:flex-row flex-col justify-between mb-8">
    <Breadcrumb pageTitle="SEO" :backTo="{ name: 'setting' }" :items="breadcrumb" />
    <Button color="teal" view="smooth" :to="{name: 'settingSeoCreate'}" link> <i class="material-icons text-lg">add</i> Add Meta Page</Button>
  </div>
  <Card v-loading="loading.all">
    <div class="flex flex-col mt-4 mb-4">
      <div class="flex flex-row 2xl:flex-nowrap xl:flex-nowrap lg:flex-nowrap flex-wrap">
        <div class="flex-grow md:mr-3 md:mb-0 mb-3">
          <Input v-model="data.search" placeholder="Search everything" class="w-full" icon>
            <template #icon>
              <span class="material-icons">search</span>
            </template>
          </Input>
        </div>
      </div>
    </div>
    <Alert color="orange" view="smooth" dense class="mb-5">
      <div class="flex items-center h-full">
        <p class="text-lg">
          These will override any SEO rules made in the page structure rules below
        </p>
      </div>
      <template #left>
        <span class="material-icons text-4xl">info</span>
      </template>
    </Alert>
    <div class="flex flex-col">
      <Table :data="state.data" v-loading="loading.all">
        <TableColumn prop="title" label="Title" />
        <TableColumn prop="url" label="Url" />
        <TableColumn label="">
          <template #default="scoped">
            <div class="flex items-center justify-center lg:opacity-0 group-hover:opacity-100 transition-opacity">
              <Button @click="editMeta(scoped.row.id)" view="smooth" color="blue" class="mx-2" dense>
                <span class="material-icons text-xl">edit</span>
              </Button>
               <Button @click="deleteMeta(scoped.row.id)" :loading="loading.delete[scoped.row.id]" view="smooth" color="red" class="mx-2" dense>
                <span class="material-icons text-xl">delete</span>
              </Button>
            </div>
          </template>
        </TableColumn>
      </Table>
      <Pagination v-model:page="data.page" v-model:limit="data.limit" :data="state" />
    </div>
  </Card>
  <Card class="mt-6">
    <div class="font-semibold mb-1">
      Page Structure SEO Rules
    </div>
    <Select v-model="selectedMeta" class="mb-4 w-56" :items="mapSeoV2" name="name" @change="changeMeta" />
    <SeoFormV2 v-if="selectedMeta" v-model:data="metaPayload" ref="refSeoFormV2" @data-changed="dataChanged" />
    <Table v-if="selectedMeta" class="mt-4" :data="stateV2[selectedMeta.value].short_code">
      <TableColumn label="Code">
        <template #default="scope">
          <div class="flex">
            <span class="mr-2">{{scope.row.code}}</span>
            <i class="material-symbols-outlined text-lg text-teal-04" @click="copyText(scope.row.code)">content_copy</i>
          </div>
        </template>
      </TableColumn>
      <TableColumn prop="description" label="Description" />
    </Table>
  </Card>
</template>

<script>
import { defineComponent, watch, ref, reactive } from 'vue'
import { useSeo } from 'composable/setting'
import { useRouter } from 'vue-router'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Select from 'bbUi/components/select/select.vue'
import Pagination from 'components/Pagination.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import SeoFormV2 from 'components/Seo/FormV2.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import debounce from 'utils/debounce'
import { upperCaseFirstLetter } from 'utils/string'
import { deepClone } from 'utils/deep-clone'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Card,
    Input,
    Select,
    Button,
    Breadcrumb,
    Pagination,
    SeoFormV2,
    Alert
  },
  async setup(_, { emit }) {
    const { state, stateV2, data, loading, actionAll, actionAllV2, actionDelete, actionUpdate } = useSeo()
    const router = useRouter()
    const refSeoFormV2 = ref(null)
    const selectedMeta = ref(null)
    const metaPayload = reactive({
      description: '',
      keywords: [],
      title: '',
      name: '',
      index: null
    })
    const breadcrumb = [{
      text: 'Settings',
      to: {
        name: 'setting'
      }
    }, {
      text: 'SEO'
    }]

    await Promise.all([
      actionAll(),
      actionAllV2()
    ])

    const mapSeoV2 = ref([])

    const swapElement = (items) => {
      let newItems = [
        items.find(v => v.index === 'meta_category'),
        items.find(v => v.index === 'meta_brand'),
        items.find(v => v.index === 'meta_device'),
        items.find(v => v.index === 'meta_model'),
      ]

      return newItems
    }

    const updateMapSeoV2 = () => {
      mapSeoV2.value = Object.entries(deepClone(stateV2.value)).map((v, i) => {
        const name = upperCaseFirstLetter(v[0].replace('meta_', '')).split(' ').join('')

        return {
          index: `meta_${name.toLowerCase()}`,
          name: name,
          value: v[0],
          is_save: true,
          ...v[1]
        }
      })

      mapSeoV2.value = swapElement(mapSeoV2.value, 0, 1)
    }

    updateMapSeoV2()

    const changeMeta = () => {
      metaPayload.description = selectedMeta.value.description
      metaPayload.keywords = selectedMeta.value.keywords || []
      metaPayload.title = selectedMeta.value.title
      metaPayload.name = selectedMeta.value.name
      metaPayload.index = `meta_${selectedMeta.value.name.toLowerCase()}`
    }

    const dataChanged = debounce(async () => {
      mapSeoV2.value = mapSeoV2.value.map(value => {
        if (value.index === metaPayload.index) {
          value.is_save = false
          value.keywords = metaPayload.keywords
          value.title = metaPayload.title
          value.description = metaPayload.description

          return value
        }

        return value
      })

      await updateMetaV2()
    }, 700)

    const copyText = (text) => {
      navigator.clipboard.writeText(text).then(() => {
        ElMessage({
          type: 'success',
          message: 'Text copied to clipboard'
        })
      })
    }

    const editMeta = (metaId) => {
      router.push({
        name: 'settingSeoEdit',
        params: {
          id: metaId
        }
      })
    }
    const updateMetaV2 = async () => {
      const isValidated = refSeoFormV2.value.validate()

      if (!isValidated) {
        return
      }

      const metaName = selectedMeta.value.value

      await actionUpdate({
        [`${metaName}_description`]: metaPayload.description,
        [`${metaName}_keywords`]: JSON.stringify(metaPayload.keywords),
        [`${metaName}_title`]: metaPayload.title
      }, async () => {
        ElMessage({
          type: 'success',
          message: 'SEO meta updated!'
        })
        await actionAllV2()
        updateMapSeoV2()
        selectedMeta.value = mapSeoV2.value.find(element => element.index === metaPayload.index)
        mapSeoV2.value.find(element => element.index === metaPayload.index).is_save = true
      })
    }
    const deleteMeta = async (metaId) => {
      await actionDelete(metaId, async () => {
        ElMessage({
          type: 'success',
          message: 'SEO updated!'
        })
        await actionAll()
      })
    }
    watch(() => ({search: data.search}), () => {
      data.page = 1
    })
    watch(() => ({...data}), debounce(async () => {
      await actionAll()
    }, 300), {
      deep: true
    })
    return {
      state,
      stateV2,
      data,
      refSeoFormV2,
      selectedMeta,
      metaPayload,
      mapSeoV2,
      changeMeta,
      copyText,
      loading,
      dataChanged,
      breadcrumb,
      router,
      editMeta,
      updateMetaV2,
      deleteMeta
    }
  }
})
</script>