<template>
  <div class="grid grid-cols-3 gap-4">
    <div class="col-span-1">
      <Input v-model="data.title" :view="validation.title.$invalid ? 'danger' : 'default'" type="text" label="Title*" @input="dataChanged" block />
      <small class="text-red-base mt-2 block" v-for="(error, errorIndex) of validation.title.$errors" :key="errorIndex">{{ error.$message }}. </small>
    </div>
    <div class="col-span-2 col-start-1">
      <Input v-model="data.description" :view="validation.description.$invalid ? 'danger' : 'default'" type="textarea" label="Description" @input="dataChanged" block />
      <small class="text-red-base mt-2 block" v-for="(error, errorIndex) of validation.description.$errors" :key="errorIndex">{{ error.$message }}. </small>
    </div>
    <div class="col-span-1 col-start-1">
      <span class="font-bold block mb-1">Keyword</span>
      <div class="border border-grey-01 p-4 rounded">
        <Button v-for="(value, keywordIndex) of data.keywords" :key="keywordIndex" class="mr-2 my-2" color="grey" view="smooth" dense>{{ value }} <i @click="removeKeyword(keywordIndex)" class="material-icons text-lg ml-1">close</i> </Button>
        <div class="flex flex-row items-center mt-4">
          <Input v-model="keywords" @keyup.enter="addKeyword" block />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive, ref } from 'vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import { useVuelidate } from '@vuelidate/core'
import { required } from '@/validations/index'

export default defineComponent({
  name: 'Company',
  components: {
    Input,
    Button
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        description: '',
        keywords: [],
        title: '',
      })
    }
  },
  emits: ['data-changed'],
  setup(props, { emit }) {
    const data = reactive(props.data)
    const rules = {
      description: {},
      title: { required },
      keywords: {}
    }
    const keywords = ref('')
    const validation = useVuelidate(rules, data, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }
    const removeKeyword = (keywordIndex) => {
      dataChanged()
      data.keywords.splice(keywordIndex, 1)
    }
    const addKeyword = () => {
      if(keywords.value) data.keywords.push(keywords.value)
      dataChanged()
      keywords.value = ''
    }
    const dataChanged = () => {
      emit('data-changed')
    }

    return {
      data,
      validation,
      keywords,
      validate,
      removeKeyword,
      addKeyword,
      dataChanged
    }
  },
})
</script>